import React from 'react';
import {connect} from "react-redux";
import {ChromePicker} from 'react-color'

function mapDispatchToProps(dispatch) {
    return {
        update: nameAndValue => {
            dispatch({update: nameAndValue, type: nameAndValue.updateType})
        }
    };
}

const mapJsonStateToProps = state => {
    return {xmlOptions: state.xmlOptions};
};

const mapXmlStateToProps = state => {
    return {jsonOptions: state.jsonOptions};
};

const popover = {
    position: 'absolute',
    zIndex: '1000',
};
const cover = {
    position: 'fixed',
    top: '0px',
    right: '0px',
    bottom: '0px',
    left: '0px',
};

class ColorPropertyComponent extends React.Component {

    constructor(props, context) {
        super(props, context);
        this.colorId = props.colorId;
        this.name = props.name;
        this.schemaDefaultValue = props.schemaDefaultValue == null ? null : props.schemaDefaultValue.substring(1);
        this.title = props.title || this.name;
        this.group = props.group;
        this.updateType = props.updateType;
        let value = typeof props.colorId == "string" ? props.colorId.substring(1) : "";
        this.state = {
            value: value
        };
    }

    updateValue = (e) => {
        e.preventDefault();
        let value = e.target.value;
        if (value !== this.state.value) {
            const newState = {
                value: value
            };
            this.fireStateUpdate(newState);
        }
    };

    fireStateUpdate = (newState) => {
        this.setState(newState);
        this.props.update({
            name: this.name,
            value: newState.value,
            group: this.group,
            updateType: this.updateType
        });
    };

    handleClick = () => {
        const newState = {
            value: this.state.value,
            displayColorPicker: true
        };
        this.setState(newState);
    };

    handlePickerChange = (color) => {
        if (color.hex == null) return;
        let value = color.hex.toUpperCase().substring(1);
        const newState = {
            value: value
        };

        this.fireStateUpdate(newState, false);
    };

    handleClose = () => {
        const newState = {
            value: this.state.value,
            displayColorPicker: false
        };
        this.fireStateUpdate(newState, false);
    };

    resetOption = () => {
        const newState = {
            value: this.schemaDefaultValue
        };
        this.fireStateUpdate(newState);
    }

    render() {
        let value = "#" + this.state.value;

        let picker = this.state.displayColorPicker
            ? <div style={popover}>
                <div onMouseUp={this.handleClose} style={cover}/>
                <ChromePicker
                    color={value}
                    // onChange={this.handleChange}
                    onChangeComplete={this.handlePickerChange}
                />
            </div>
            : null;

        let style = {"backgroundColor": value, "borderColor": value};
        return (
            <div>
                <small className="form-text text-dark">{this.title}</small>
                <div className="input-group color-picker">
                    <div className="input-group-prepend">
                        <button onClick={this.handleClick} className="btn" type="button"
                                style={style}>
                            {picker}
                        </button>
                    </div>

                    <input type="text" className="form-control form-control-sm color-property"
                           value={this.state.value}
                           onChange={this.updateValue}/>
                    {this.schemaDefaultValue == null || (this.state.value === this.schemaDefaultValue) ? null : <a onClick={this.resetOption} href="#"><small>revert</small></a>}

                </div>
            </div>
        )
    }
}

const RColorPropertyComponent = connect(mapJsonStateToProps, mapDispatchToProps)(ColorPropertyComponent);
const RXmlColorPropertyComponent = connect(mapXmlStateToProps, mapDispatchToProps)(ColorPropertyComponent);

export {RColorPropertyComponent as ColorPropertyComponent, RXmlColorPropertyComponent as XmlColorPropertyComponent}
