import React from 'react';
import {ColorPropertyComponent} from "./ColorPropertyComponent";
import {connect} from "react-redux";
import {renderAccordionElement} from "../helper/renderer";
import defaultJsonOptions from "../store/defaultJsonOptions.js";

const mapStateToProps = state => {
    return {jsonOptions: state.jsonOptions};
};

const accordionId = "generalSettings";

class GeneralElementsComponent extends React.Component {

    constructor(props, context) {
        super(props, context);
        let jsonOptions = this.props.jsonOptions;

        this.resultGroups = [];

        let keys = Object.keys(jsonOptions);
        let sortedKeys = keys.sort();


        let count = 0;
        for (const groupKey of sortedKeys) {
            count++;
            let subObject = jsonOptions[groupKey];
            if (subObject == null) continue;

            let subKeys = Object.keys(subObject);
            const subElements = [];
            let subCount = 0;
            for (let propertyKey of subKeys) {
                let defaultValue = defaultJsonOptions[groupKey][propertyKey];

                let key = "general-item" + ++subCount;
                subElements.push(
                    <ColorPropertyComponent key={key}
                                            colorId={subObject[propertyKey]}
                                            group={groupKey}
                                            schemaDefaultValue={defaultValue}
                                            updateType="json"
                                            name={propertyKey}/>
                )
            }

            this.resultGroups.push(renderAccordionElement(count, groupKey, subElements, accordionId));
        }
    }

    render() {
        return (
            <div className="col-3">
                <div className="accordion" id={accordionId}>
                    {this.resultGroups}
                </div>
            </div>
        )
    }
}

const RGeneralElementsComponent = connect(mapStateToProps)(GeneralElementsComponent);

export {RGeneralElementsComponent as GeneralElementsComponent}
