import React from "react";

export function renderAccordionElement(uniqueId, groupName, subElements, accordionId) {
    return <div key={"group" + accordionId + uniqueId} className="card">
        <div className="card-header" id={"heading" + accordionId + uniqueId}>
            <button className="btn btn-block text-left collapsed" data-toggle="collapse"
                    data-target={"#collapse" + accordionId + uniqueId}
                    aria-expanded="false" aria-controls={"collapse" + accordionId + uniqueId}>
                {groupName}
                <span className="oi oi-chevron-bottom float-right"/>
            </button>
        </div>

        <div id={"collapse" + accordionId + uniqueId}
             className="collapse" aria-labelledby={"heading" + accordionId + uniqueId}
             data-parent={"#" + accordionId}>
            <div className="card-body">
                {subElements}
            </div>
        </div>
    </div>
}