import React from 'react';
import {connect} from "react-redux";
import {renderAccordionElement} from "../helper/renderer";
import {ColorPropertyComponent, XmlColorPropertyComponent} from "./ColorPropertyComponent";
import {FontTypePropertyComponent} from "./FontTypePropertyComponent";

const accordionId = "editorSettings";

const mapStateToProps = state => {
    return {xmlOptions: state.xmlOptions};
};

class EditorElementsComponent extends React.Component {
    
    constructor(props, context) {
        super(props, context);
        let xmlOptions = this.props.xmlOptions;

        this.resultGroups = [];

        let keys = Object.keys(xmlOptions);
        let sortedKeys = keys.sort();

        let count = 0;
        for (const groupKey of sortedKeys) {
            count++;
            let subObject = xmlOptions[groupKey];
            if (subObject == null) continue;

            let elements = [];

            elements.push(<XmlColorPropertyComponent key={"editor-item1"}
                                                     colorId={subObject.FOREGROUND}
                                                     group={groupKey}
                                                     updateType="xml"
                                                     name="FOREGROUND"
                                                     title="Foreground"/>);
            elements.push(<XmlColorPropertyComponent key={"editor-item2"}
                                                     colorId={subObject.BACKGROUND}
                                                     group={groupKey}
                                                     updateType="xml"
                                                     name="BACKGROUND"
                                                     title="Background"/>);

            elements.push(<FontTypePropertyComponent key={"editor-item3"}
                                                     fontType={subObject.FONT_TYPE}
                                                     group={groupKey}
                                                     updateType="xml"
                                                     name="FONT_TYPE"
                                                     title="Bold"/>);

            this.resultGroups.push(renderAccordionElement(count, subObject.NAME, elements, accordionId));
        }
    }

    render() {
        return (
            <div className="col-3">
                <div className="accordion" id={accordionId}>
                    {this.resultGroups}
                </div>
            </div>
        )
    }
}

const REditorElementsComponent = connect(mapStateToProps)(EditorElementsComponent);

export {REditorElementsComponent as EditorElementsComponent}
