import React from 'react'

export default function (editorOptions) {
    
    const defaultTextBackground = `background-color: ${getBackgroundColor(editorOptions)}; font-family:'Menlo Regular';font-weight:normal;font-size:14px;line-height:1.5;`;
    const keywordStyle = getTextStyle(editorOptions, "DEFAULT_KEYWORD");
    const lineComment = getTextStyle(editorOptions, "DEFAULT_LINE_COMMENT");
    const defaultText = getTextStyle(editorOptions, "TEXT");
    const stringLiteral = getTextStyle(editorOptions, "DEFAULT_STRING");
    return `
<div id="editor" style="${defaultTextBackground}">
<pre><span style="${keywordStyle}">package </span><span style="${defaultText}">com.company</span><span class="s0">;</span>
<span style="${keywordStyle}">public class </span><span style="${defaultText}">Main {</span>
        <span style="${keywordStyle}">public static void </span><span style="${defaultText}">main(String[] args) {</span>
            <span style="${lineComment}">// write your code here</span>
            <span style="${defaultText}">System.out.println(</span><span style="${stringLiteral}">&quot;Hello&quot;</span><span
        style="${defaultText}">)</span><span style="${keywordStyle}">;</span>
        <span style="${defaultText}">}</span>
<span style="${defaultText}">}</span>
</pre>
</div>`
}

function getBackgroundColor(editorOptions) {
    return editorOptions.TEXT.BACKGROUND || "#000000";
}

function getTextStyle(editorOptions, option) {
    let styles = editorOptions[option];

    let result = `color:${styles.FOREGROUND || "#FFFFFF"};`;
    if (styles.FONT_TYPE != null) {
        if (styles.FONT_TYPE === "1") {
            result += "font-weight: bold;"
        }
    }
    if (styles.BACKGROUND) {
        result += `background-color: ${styles.BACKGROUND};`
    }
    return result;
}