import defaultOptions from '../data/darcula-default-meaningful.json'
import starProperties from '../data/starProperties.json'

const formattedOptions = {};
for (let k in defaultOptions) {
    if (defaultOptions.hasOwnProperty(k)) {
        formattedOptions[k] = getFlattenProperties(defaultOptions[k], "");
    }
}

if (Object.keys(starProperties).length > 0) {
    formattedOptions["*"] = getFlattenProperties(starProperties, "");
}

function getFlattenProperties(obj, prefix) {
    let result = {};
    if (prefix) prefix = prefix + ".";
    for (let key in obj) {
        if (!obj.hasOwnProperty(key)) continue;

        let value = obj[key];
        if (typeof value !== "object") {
            result[prefix + key] = value;
            continue;
        }

        let flattenProperties = getFlattenProperties(value, prefix + key);
        result = Object.assign(result, flattenProperties);
    }

    return result;
}

function freezeRecursively(obj) {
    Object.freeze(obj);
    for (let k in obj) {
        if (typeof obj == "object" && obj.hasOwnProperty(k)) {
            freezeRecursively(obj[k]);
        }
    }
}

freezeRecursively(formattedOptions);

export default formattedOptions;
