import JSZip from 'jszip'
import {saveAs} from 'file-saver';

const uuid = require('uuid/v1');

const EDITOR_SCHEMA_NAME = "MyEditorScheme.xml";
const MY_THEME_FILE = "MyTheme.theme.json";

export function generateZip(state, pluginName, author) {
    const topLevelZip = new JSZip();
    let metaInf = topLevelZip.folder("META-INF");
    generateManifestMF(metaInf);
    generatePluginXml(metaInf, pluginName, author);

    generateThemeJson(topLevelZip, state, pluginName, author);
    let themes = topLevelZip.folder("themes");
    generateEditorScheme(themes, state, pluginName)

    topLevelZip.generateAsync({type: "blob"})
        .then(function (content) {
            saveAs(content, pluginName + ".jar");
        });
}

function generateEditorScheme(themes, state, pluginName) {
    let result = `<scheme name="${pluginName}" version="1" parent_scheme="Darcula">\n`;
    result += "<attributes>\n";

    const xmlOptions = state.xmlOptions;
    for (const name in xmlOptions) {
        if (xmlOptions.hasOwnProperty(name)) {
            result += generateAttribute(name, xmlOptions[name])
        }
    }

    result += "</attributes>\n";
    result += "</scheme>";

    themes.file(EDITOR_SCHEMA_NAME, result);
}

function generateAttribute(name, values) {

    let result = `<option name="${name}">\n`;
    result += "<value>\n";

    for (const key of Object.keys(values)) {
        let value = values[key];
        if (value != null && key !== "NAME") {
            if (value.startsWith("#")) {
                value = value.substring(1);
            }
            result += `<option name="${key}" value="${value}" />\n`;
        }
    }

    result += "</value>\n";
    result += "</option>";

    return result;
}


function generateThemeJson(topLevelZip, state, pluginName, author = "Author") {
    const result = {};
    result.name = pluginName;
    result.author = author;
    result.dark = true;
    result.editorScheme = `/themes/${EDITOR_SCHEMA_NAME}`;

    let uiOptions = {};
    result.ui = uiOptions;

    for (const name in state.jsonOptions) {
        if (state.jsonOptions.hasOwnProperty(name) && name !== "*") {
            uiOptions[name] = state.jsonOptions[name];
        }
    }

    let s = JSON.stringify(result);
    topLevelZip.file(MY_THEME_FILE, s);
}

function generateManifestMF(metaInf) {
    metaInf.file("MANIFEST.MF", "Manifest-Version: 1.0\n" +
        "Created-By: Your Great Theme\n");
}

function generatePluginXml(metaInf, pluginName, author = "Author", pluginId = "great.theme", description = "Generated plugin") {
    let uuidValue = uuid();

    const content = `<idea-plugin>
  <id>${pluginId}</id>
  <name>${pluginName}</name>
  <version>1.0</version>
  <vendor>${author}</vendor>

  <description><![CDATA[
  ${description}
    ]]></description>

  <idea-version since-build="192.0"/>
  <depends>com.intellij.modules.lang</depends>
  <extensions defaultExtensionNs="com.intellij">
    <themeProvider id="${uuidValue}" path="/${MY_THEME_FILE}"/>
  </extensions>

</idea-plugin>`;
    metaInf.file("plugin.xml", content);
}