import React from 'react';
import store from '../store/store';
import {generateZip} from "../helper/generateZip";

class CreatePluginComponent extends React.Component {
    constructor(props, context) {
        super(props, context);

        this.state = {
            pluginName: "Your great theme"
        }
    };

    onSubmit = (e) => {
        e.preventDefault();
        let state = store.getState();
        generateZip(state, this.state.pluginName);
    };

    pluginName = (e) => {
        this.setState({
            pluginName: e.target.value
        })
    };

    render() {
        return <div className="col-12">
            <form>
                <div className="form-group">
                    <label htmlFor="pluginName">Plugin name</label>
                    <input type="text" className="form-control w-25" id="pluginName" onChange={this.pluginName}
                           value={this.state.pluginName}/>
                </div>

                <button className="btn btn-warning" onClick={this.onSubmit}>Generate plugin</button>
            </form>
        </div>;
    }
}


export {CreatePluginComponent}
