import {createStore} from 'redux'
import defaultJsonOptions from "./defaultJsonOptions";
import defaultEditorOptions from "./defaultEditorOptions";

function updateProperty(state = {
    jsonOptions: defaultJsonOptions,
    xmlOptions: defaultEditorOptions
}, action) {
    let update = action.update;
    
    if (action.type === "reload") {
        return JSON.parse(JSON.stringify(state));
    }
    
    if (action.type === "json") {
        if (update && update.value.length <= 6) {
            const newOptions = shadowCopy(state.jsonOptions);
            newOptions[update.group][update.name] = "#" + update.value;
            return {jsonOptions: newOptions, xmlOptions: state.xmlOptions}
        }
    } else if (action.type === "xml") {
        if (update) {
            if (update.value && update.value.length === 6) {
                const newOptions = shadowCopy(state.xmlOptions);
                newOptions[update.group][update.name] = "#" + update.value;
                console.log("state updated " + update.name + " " + update.value);
                return {xmlOptions: newOptions, jsonOptions: state.jsonOptions}
            } else if (update.name === "FONT_TYPE") {
                const newOptions = shadowCopy(state.xmlOptions);
                newOptions[update.group][update.name] = update.fontType;
                return {xmlOptions: newOptions, jsonOptions: state.jsonOptions}
            }
        }
    }
    return state;
}


function shadowCopy(state) {
    let result = {};
    return Object.assign(result, state);
}


export default createStore(updateProperty);
