import React from 'react';
import {connect} from "react-redux";

function mapDispatchToProps(dispatch) {
    return {
        update: nameAndValue => {
            dispatch({update: nameAndValue, type: nameAndValue.updateType})
        }
    };
}

class FontTypePropertyComponent extends React.Component {
    constructor(props, context) {
        super(props, context);
        this.name = props.name;
        this.title = props.title || this.name;
        this.group = props.group;
        this.updateType = props.updateType;
        this.state = {
            fontType: props.fontType,
        };
    }

    updateValue = (e) => {
        let value = e.target.checked;
        console.log("Value: " + value);
        const newState = {
            fontType: value === true ? "1" : undefined
        };
        this.fireStateUpdate(newState);
    };

    fireStateUpdate = (newState) => {
        this.setState(newState);
        this.props.update({
            name: this.name,
            fontType: newState.fontType,
            group: this.group,
            updateType: this.updateType
        });
    };
    
    render() {
        return (
            <div>
                <label>
                    <input type="checkbox" className="mr-1"
                           checked={this.state.fontType === "1"}
                           onChange={this.updateValue}/>
                    <small className="text-dark">{this.title}</small>

                </label>
            </div>
        )
    }
}

const RFontTypePropertyComponent = connect(null, mapDispatchToProps)(FontTypePropertyComponent);

export {RFontTypePropertyComponent as FontTypePropertyComponent}