import React from 'react';
import {connect} from "react-redux";
import textTemplate from "../helper/textTemplate";


const mapStateToProps = state => {
    return {xmlOptions: state.xmlOptions};
};

class EditorTextComponent extends React.Component {

    render() {
        let template = textTemplate(this.props.xmlOptions);

        return <div dangerouslySetInnerHTML={{__html: template}}/>;
    }
}


const REditorTextComponent = connect(mapStateToProps)(EditorTextComponent);

export {REditorTextComponent as EditorTextComponent}